.section-booking {
  margin-top: 7rem;
  padding: 8.5rem 12rem;
  background-color: green;
  &__container {
    background-color: grey;
  }
  &__form-container {
    width: fit-content;
    clip-path: polygon(0 0, 100% 0, 75% 100%, 0% 100%);
    background-color: white;
    padding: 4rem 4rem;
    padding-right: 10rem;
    .heading-secondary {
      &:hover {
        transform: none;
        text-shadow: none;
      }
    }
    .section-booking__form {
      &__input {
        &--text {
          input {
            height: 4rem;
            width: 36rem;
            background-color: beige;
            border: none;
            padding: 1rem;
            transition: box-shadow 0.2s ease-in;
          }
          :first-of-type {
            margin-bottom: 2rem;
          }
          :focus-visible {
            outline-offset: unset;
            outline: none;

            //border: 0px;
            //border-bottom: 3px solid;
            //border-color: orange;
            //box-shadow: 0 1rem 2rem rgb(0 0 0 / 20%);
            box-shadow: 0px -1px 5px 0px rgba(0 0 0/20%),
              0px 3px 0px 0px rgba(231, 166, 26, 1);
          }
        }
      }
    }
  }
}
